<template>
  <div class="bidding">
    <Crumbs
      :parent-manage="'实施管理'"
      :children-manage="'招投标管理'"
      @isShowScreen="isShowScreen"
    ></Crumbs>

    <!-- 筛选内容 -->
    <Content
      v-if="isScreen"
      class="content screenStyle"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="searchData"
        :label-width="85"
      >
        <Row>
          <Col span="11">
          <FormItem label="招标名称">
            <Input
              v-model.trim="searchData.bidding_name"
              placeholder="请填写招标名称"
            ></Input>
          </FormItem>
          </Col>
          <Col span="11" offset="2">
          <FormItem label="中标单位">
            <Input
              v-model.trim="searchData.winning_bid_org"
              placeholder="请填写中标单位"
            ></Input>
          </FormItem>
          </Col>
        </Row>
        <!-- 提交开始和结束 -->
        <Row style="margin-top:15px;">
          <Col span="11">
          <FormItem label="开标开始时间">
            <DatePicker
              v-model="searchData.startTime"
              style="width:100%;"
              type="date"
              :options="startTimeOptions"
              placeholder="请选择开标开始时间"
              placement="bottom-start"
              @on-change="startTimeChange"
            ></DatePicker>
          </FormItem>
          </Col>
          <Col span="11" offset="2">
          <FormItem label="开标结束时间">
            <DatePicker
              v-model="searchData.endTime"
              style="width:100%;"
              type="date"
              :options="endTimeOptions"
              placeholder="请选择开标结束时间"
              placement="bottom-start"
              @on-change="endTimeChange"
            ></DatePicker>
          </FormItem>
          </Col>
        </Row>
        <Row style="margin-top:15px;text-align: right;">
          <Button type="primary" @click="searchList">确认</Button>
        </Row>
      </Form>
    </Content>
    <!-- /筛选内容 -->

    <!-- table-content -->
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button
        type="primary"
        icon="md-add"
        @click="handleAdd"
      >
        新增
      </Button>
      <Button
        type="primary"
        icon="ios-create"
        @click="handleModify"
      >
        修改
      </Button>
      <Button
        type="primary"
        icon="md-close"
        @click="handleDelete"
      >
        删除
      </Button>
      <Button icon="md-print" @click.stop="printContent(17)">
        打印
      </Button>
      <Button icon="md-exit" @click="excelExcel('/bidding/exportBiddingListExcel')">
        导出
      </Button>
      <Table
        highlight-row
        class="table"
        :columns="columns"
        :data="binddingList"
        style="margin-top: 15px;"
        @on-current-change="selectRow"
      ></Table>
      <!-- 分页组件 -->
      <Page
        style="text-align: right"
        :total="total"
        :current="searchData.pageNo"
        :page-size="searchData.pageSize"
        show-sizer
        transfer
        @on-change="pageNoChange"
        @on-page-size-change="pageSizeChange"
      />
    </Content>
    <!--审核的弹出框-->
    <audit ref="audit" :form="auditForm" />
    <!-- 招投标弹窗 -->
    <biddModal ref="biddModal" :is-create="isCreate" :is-disabled="isDisabled" :organizational-form="organizationalForm" :bill-type="billType" @refreshList="searchList" />
  </div>
</template>

<script>
import Crumbs from '../components/Crumbs';
import biddModal from './components/biddModal.vue';
import tablePage from '@/mixins/tablePage.js';
import audit from '@/components/audit.vue'
export default {
  name: 'Bidding',
  components: { Crumbs, biddModal, audit },
  mixins: [tablePage],
  data() {
    return {
      // 组织形式
      organizationalForm: {
        1: '公开',
        2: '竞争性磋商',
        3: '竞争性谈判'
      },
      // 招标类型
      billType: {
        1: '政采',
        2: '其他'
      },
      // 控制筛选框显示隐藏  默认隐藏
      isScreen: false,
      // 筛选内容_字段
      searchData: {
        bidding_name: '',
        winning_bid_org: '',
        ascending: false,
        orderBy: 'id',
        pageNo: 1,
        pageSize: 10
      },
      // 表格内容 字段相对应
      binddingList: [],
      // 弹出框内容显示隐藏   默认隐藏
      selTypeMod: false,
      // 控制 是否禁用  默认不禁用
      isDisabled: false,
      listUrl: '/bidding/searchBidding/list',
      columns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          fixed: 'left',
          indexMethod: this.indexMethod
        },
        {
          title: '标段名称',
          key: 'bidding_name',
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  this.$nextTick(() => {
                    this.isDisabled = true;
                    this.$refs.biddModal.showModal(params.row.id);
                  })
                }
              }
            }, params.row.bidding_name);
          }
        },
        {
          title: '组织形式',
          key: 'organizational_form',
          render: (h, params) => {
            return h('div', this.organizationalForm[params.row.organizational_form])
          }
        },
        {
          title: '编号',
          key: 'code'
        },
        {
          title: '招标类型',
          key: 'bidding_type',
          render: (h, params) => {
            return h('div', this.billType[params.row.bidding_type])
          }
        },
        {
          title: '开标时间',
          key: 'start_bidding_time',
          render: (h, params) => {
            return h('div', this.$moment(params.row.start_bidding_time).format('YYYY年MM月DD日'))
          }
        },
        {
          title: '招标代理机构',
          key: 'proxy_org'
        },
        {
          title: '中标单位',
          key: 'winning_bid_org'
        },
        {
          title: '中标金额',
          key: 'winning_bid_money'
        },
        {
          align: 'center',
          title: '审核状态',
          key: 'auditStatus',
          fixed: 'right',
          width: 120,
          render: (h, params) => {
            return this.getAuditDetail(h, params)
          }
        }
      ]
    }
  },
  created() {
    this.searchList()
  },
  methods: {
    searchList() {
      const that = this;
      that.search(that.getNewSearchData(), (flag, res) => {
        if (flag) {
          const data = res.data
          that.binddingList = data.binddingList
          that.total = data.totalCount
        }
      })
    },
    // 子组件接收 控制筛选内容的隐藏和显示
    isShowScreen(msg) {
      this.isScreen = msg;
    },
    // 添加批复 弹出框显示
    handleAdd() {
      this.isCreate = true;
      this.isDisabled = false;
      this.$refs.biddModal.showModal();
    },
    // 更新操作
    handleModify() {
      const that = this;
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要修改的内容');
        return
      }
      const form = Object.assign({}, that.currentRow);
      // 未审核 和 审核驳回可以更改信息
      const flag = [2, 3].indexOf(form.auditStatus) !== -1
      that.$nextTick(() => {
        that.isCreate = false;
        that.isDisabled = !flag;
        that.$refs.biddModal.showModal(form.id);
      })
    },
    // 打印操作
    handlePrint() { },
    // 导出操作
    handleExport() { },
    handleDelete() {
      this.deleteData('/bidding/deleteBidding/', 'id')
    }
  }
}
</script>



